<template>
  <div class="imgcheck ">
    <div class="form-group ">
        <input type="file" class="form-control" id="file" @change="filechange($event)" style="display: none"  />

    </div>

    <div class="input-group">
      <input type="text" class="form-control" aria-label="..." v-model="imgurl" placeholder="请输入网络图片url" />
      <div class="input-group-btn">
        <button type="button" class="btn btn-default" v-on:click="urlcheck()">检测图片</button>
        <button type="button" class="btn btn-default" v-on:click="upimgfile()">图片上传</button>
      </div>

    </div>

    <div class="row showbox">
      <div class="imgbox col-md-5">
        <img :src="imgurl" alt="" class="img-responsive">
      </div>

      <div class="col-md-7">
        <textarea name="showtext" v-model="showtext" class="form-control" style="width: 100%;height: 300px" ></textarea>
      </div>

    </div>

  </div>

</template>

<script>
export default {
  name: 'imgcheck',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      showtext:'',
      imgurl:'请输入网络图片url'
    }
  },
  methods:{
    upimgfile:function(){
      document.getElementById('file').click();
    },
    filechange:function(e){
      var that = this;
      //console.log(e);
      var formdata = new FormData();
      formdata.append('upimgfile', e.target.files[0]);

      function parseJSON(response) {
        return response.json()
      }

      fetch('http://localhost:3000/upload',{
        method:'post',
        body:formdata
      })
        .then(parseJSON)
        .then(function(data){
          console.log(data);
          that.showtext = data.text;
          that.imgurl = data.file;


      })
    },
    urlcheck:function(){
      var that = this;
      var url = this.imgurl;
      var reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?.(jpg|png|jpeg|bmp)/;

      function parseJSON(response) {
        return response.json()
      }
      if(!reg.test(url)){
        alert('输入的网址不合法');
      }else{
        fetch('http://localhost:3000/urlocr',{
          method:'post',
          mode:'cors',
          body:JSON.stringify({
            imgurl:url
          }),
          headers:{
            'Content-Type':'application/json'
          }

        })
          .then(parseJSON)
          .then(function(data){
            that.showtext = data.text;
            that.imgurl = data.file;
          })
      };
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.imgbox{

  /*width:300px;*/
  height:300px;
  line-height: 300px;
  text-align: center;
  border: 1px solid #c0c0c0;
  overflow: auto;
  margin-bottom: 15px;
}

  .showbox{
    padding-top: 20px;
  }
  .urlinput{
    margin-bottom: 15px;
  }
</style>



